	module.exports = "<h1 id=\"location\"><a class=\"header-anchor\" href=\"#location\" aria-hidden=\"true\">#</a> Location</h1>\n<p>A type of object produced and consumed by the <a href=\"https://github.com/mjackson/history\">history</a> package, representing one state of the browsers URL and HTML5 History.</p>\n<p><code>Location</code> objects are plain-old JavaScript. You can create them with object literals.</p>\n<h2 id=\"properties\"><a class=\"header-anchor\" href=\"#properties\" aria-hidden=\"true\">#</a> Properties</h2>\n<ul>\n<li><code>pathname</code> (<em>string</em>)</li>\n<li><code>search</code> (<em>string</em>)</li>\n<li><code>hash</code> (<em>string</em>)</li>\n<li><code>state</code> (<em>object</em>)</li>\n</ul>\n<h2 id=\"example\"><a class=\"header-anchor\" href=\"#example\" aria-hidden=\"true\">#</a> Example</h2>\n<pre><code class=\"language-js\"><span class=\"token keyword\" >const</span> myLocation <span class=\"token operator\" >=</span> <span class=\"token punctuation\" >{</span>\n  <span class=\"token comment\" spellcheck=\"true\">// These two parts correspond to the URL</span>\n  pathname<span class=\"token punctuation\" >:</span> <span class=\"token string\" >'/contact/15/payments'</span><span class=\"token punctuation\" >,</span>\n  search<span class=\"token punctuation\" >:</span> <span class=\"token string\" >'?order=date&amp;where=paid:false'</span>\n\n  <span class=\"token comment\" spellcheck=\"true\">// This part corresponds to the HTML5 History state</span>\n  state<span class=\"token punctuation\" >:</span> <span class=\"token punctuation\" >{</span>\n\n    <span class=\"token comment\" spellcheck=\"true\">// Junctions stores any information which cannot fit in the URL under a</span>\n    <span class=\"token comment\" spellcheck=\"true\">// `$$junctions` key within HTML5 History state</span>\n    $$junctions<span class=\"token punctuation\" >:</span> <span class=\"token punctuation\" >{</span>\n      <span class=\"token string\" >'main'</span><span class=\"token punctuation\" >:</span> <span class=\"token punctuation\" >{</span> branchKey<span class=\"token punctuation\" >:</span> <span class=\"token string\" >'AddContactModal'</span> <span class=\"token punctuation\" >}</span><span class=\"token punctuation\" >,</span>\n    <span class=\"token punctuation\" >}</span>\n  <span class=\"token punctuation\" >}</span>\n<span class=\"token punctuation\" >}</span>\n</code></pre>\n"

