	module.exports = "<p><strong>Note: The Guide is still only an outline. PRs would be greatly appreciated  just open an issue first to make sure there is no duplication of work.</strong></p>\n<h1 id=\"locations-routes-and-maps\"><a class=\"header-anchor\" href=\"#locations-routes-and-maps\" aria-hidden=\"true\">#</a> Locations, Routes and Maps</h1>\n<ul>\n<li>Routers are a way of adding structure to an application</li>\n<li>We say this back in Do I need a router?</li>\n<li>But why is it that applications need this extra structure?</li>\n<li>The answer stems from our first two principles</li>\n</ul>\n<h2 id=\"locations-aint-routes\"><a class=\"header-anchor\" href=\"#locations-aint-routes\" aria-hidden=\"true\">#</a> Locations Aint Routes</h2>\n<ul>\n<li>The browser stores state in a flat format, while composable components require relative routes. and whats more - those routes cant be url-based</li>\n<li>Code examples</li>\n<li>There is a mismatch between the structure of the navigation state needed by components, and the structure where it is stored in the browser</li>\n<li>We have a Location, we need a Route</li>\n<li>This is the source of the difficulty with structure we encountered back in Do I Need A Router?</li>\n</ul>\n<h2 id=\"routers-find-routes\"><a class=\"header-anchor\" href=\"#routers-find-routes\" aria-hidden=\"true\">#</a> Routers Find Routes</h2>\n<ul>\n<li>\n<p>This is where a router comes in. It helps to convert between the shape of data our browser expects (e.g. a Location), and that which our application requires (a Route)</p>\n</li>\n<li>\n<p>So how do routers go about this conversion?</p>\n</li>\n<li>\n<p>To find out, lets first consider what it is that locations and routes represents.</p>\n</li>\n<li>\n<p>Consider the difference between the meanings of the words Location and Route in another context</p>\n</li>\n<li>\n<p>Locations are a place. Routes are how to get there.</p>\n</li>\n<li>\n<p>Image: location vs routes (draw multiple routes for the one location)</p>\n</li>\n<li>\n<p>Similarly, Locations represent a single state of your application tree</p>\n</li>\n<li>\n<p>Routes contain the actual information on how to render it</p>\n</li>\n<li>\n<p>Image: component tree vs prop flow through component tree</p>\n</li>\n<li>\n<p>With this in mind, lets return to the question of how we pick a Route for a given Location</p>\n</li>\n</ul>\n<h2 id=\"to-find-a-route-you-need-a-map\"><a class=\"header-anchor\" href=\"#to-find-a-route-you-need-a-map\" aria-hidden=\"true\">#</a> To Find A Route, You Need A Map</h2>\n<ul>\n<li>In our real world example, its simple. We need a road map.</li>\n<li>Image: tree if routes</li>\n<li>And in our application example, we need a component map. Or as wed refer to it in the Junctions library, a <em>Junction Map</em>.</li>\n<li>Image: tree of components, labelling sections with url part</li>\n<li>This image is actually generated from the Junction map of one of our example applications. And you can generate maps for your applications, too!</li>\n<li>In fact, see how the map looks like a tree of Junctions? Thats where this packages name comes from.</li>\n</ul>\n<h2 id=\"math-over-magic\"><a class=\"header-anchor\" href=\"#math-over-magic\" aria-hidden=\"true\">#</a> Math Over Magic</h2>\n<ul>\n<li>Junctions is a tool which converts Locations into Routes, and vice versa, using a map of Junctions.</li>\n<li>And thats pretty much all it does. Which might be a surprise to you. What about all the magic which other routers perform?</li>\n<li>In Junctions, <em>you dont need any magic</em> to get your routing information to where it is needed.</li>\n<li>Because of hour our data is structured, it is already there.</li>\n<li>But dont take my word for it. Lets go over the three basic data structures in Junctions: Locations, Routes and Maps.</li>\n</ul>\n"

