	module.exports = "<p><strong>Note: The Guide is still only an outline. PRs would be greatly appreciated  just open an issue first to make sure there is no duplication of work.</strong></p>\n<h1 id=\"motivation\"><a class=\"header-anchor\" href=\"#motivation\" aria-hidden=\"true\">#</a> Motivation</h1>\n<ul>\n<li>I got a request from a client - I want my screens to be reusable. And I want the app to use react-router.</li>\n<li>React components are reusable. Screens are components. And react-router is pretty standard. This seemed reasonable, so I said OK</li>\n</ul>\n<p>Things werent OK</p>\n<ul>\n<li>\n<p>As I tried to implement things, I started to run into issues</p>\n</li>\n<li>\n<p>Many of these issues stemmed from decisions made for me by react-router</p>\n<p>How do you link within screens?</p>\n<ul>\n<li>How do you link if you dont know the URL until the app is mounted?</li>\n</ul>\n<p>Re-usable components shouldnt need access to the application root</p>\n<ul>\n<li>React-router defines its routes at the top level of the application</li>\n</ul>\n<p>What if your screen doesnt have a URL?</p>\n<ul>\n<li>Modals dont need URLs</li>\n<li>But you still might want a re-usable screen within a modal</li>\n</ul>\n</li>\n</ul>\n<p>Components should be independent</p>\n<ul>\n<li>\n<p>It forces components to use its context-based interface to get information.</p>\n</li>\n<li>\n<p>It ties your components to react-router</p>\n</li>\n<li>\n<p>It steals your components independence. Theyre no longer re-usable components, theyre just react-router plugins</p>\n</li>\n<li>\n<p>What if you want to make real, reusable components?</p>\n</li>\n<li>\n<p>Components should be just that - React components</p>\n</li>\n<li>\n<p>Junctions is my toolbox for doing just this. And to do so, it follows three principles.</p>\n</li>\n</ul>\n"

